import React, { Suspense, lazy } from 'react'
import { HashRouter as Router, Routes, Route } from 'react-router-dom'
import Home from './pages/Home'
import Navigation from './components/Navigation'
import Contact from './pages/Contact'
import Disclaimer from './pages/Disclaimer'
import Acknowledgement from './pages/Acknowledgement'
import Footer from './components/Footer'
import Preloader from './components/Preloader'
import { AlertProvider } from './context/AlertContext'
import AlertMessage from "./components/AlertMessage"
import ScrollToTop from './components/ScrollToTop'
import DroughtDetails from './maps/DroughtDetails'
import QuizPage from './QuizPage/QuizPage'
import LightingDetails from './maps/LightingDetails'
import CycloneDetails from './maps/CycloneDetails'
import EpidemicsDetails from './maps/EpidemicsDetails'
import FloodDetails from './maps/FloodDetails'
import EarthquakeDetails from './maps/EarthquakeDetails'

const DroughtMap = lazy(() => import('./maps/DroughtMap'));
const EarthquakeMap = lazy(() => import('./maps/EarthquakeMap'));
const CycloneMap = lazy(() => import('./maps/CycloneMap'));
const FloodMap = lazy(() => import('./maps/FloodMap'));
const LightingMap = lazy(() => import('./maps/LightingMap'));
const EpidemicsMap = lazy(() => import('./maps/EpidemicsMap'));


const App = () => {
  return (
    <Router>
      <AlertProvider>
        <Navigation />
        <AlertMessage />

        <Suspense fallback={<Preloader />}>
          <Routes>
            <Route path='/' element={<Home />} />
            <Route path='/drought' element={<DroughtMap />} />
            <Route path='/earthquake' element={<EarthquakeMap />} />
            <Route path='/cyclone' element={<CycloneMap />} />
            <Route path='/flood' element={<FloodMap />} />
            <Route path='/lighting' element={<LightingMap />} />
            <Route path='/epidemics' element={<EpidemicsMap />} />

            <Route path='/drought/drought-details' element={<DroughtDetails />} />
            <Route path='/lighting/lighting-details' element={<LightingDetails />} />
            <Route path='/cyclone/cyclone-details' element={<CycloneDetails />} />
            <Route path='/epidemics/epidemics-details' element={<EpidemicsDetails />} />
            <Route path='/flood/flood-details' element={<FloodDetails />} />
            <Route path='/earthquake/earthquake-details' element={<EarthquakeDetails />} />

            <Route path="/contact" element={<Contact />} />
            <Route path="/disclaimer" element={<Disclaimer />} />
            <Route path="/acknowledgement" element={<Acknowledgement />} />
            <Route path="/quiz/:Name" element={<QuizPage />} />
          </Routes>

        </Suspense>
        <ScrollToTop/>
      </AlertProvider>
      <Footer />
    </Router>

  )
}

export default App